.acf-block-coach {
	/*styles en écart du bloc état d'esprit - classe accueil commune*/
	.titre {
		margin-bottom: 20px;
	}.texte {
		display: flex;
		flex-direction: column;
		justify-content: center;
	}
	.part1 {
		position: relative;
	}
	.mots {
		bottom:-40px;
		position: absolute;
		margin-bottom: 0;
	}
	@include bp(md) {
		.mots {
			bottom:-50px;
		}
		.texte {
			padding-bottom:100px;
		}
	}
}